import React, { Component } from 'react';
import axios from 'axios';
import UserForm from '../../components/userForm/userForm.component';

class EditForm extends Component {
  fields = ['name', 'email', 'password'];
  disabled = ['email', 'submit'];

  constructor(props) {
    super(props);

    this.state = {
      name: '',
      email: '',
      password: ''
    }

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    let { name, value } = event.target;
    this.setState({ [name]: value });
  }

  handleSubmit(event) {
    event.preventDefault();
    axios.post('/edit', {
      name: this.state.name,
      password: this.state.password
    });
  }

  componentDidMount() {
    this.loadUserInfoToFields();
  }

  loadUserInfoToFields() {
    this.getUserInfo().then(current => {
      this.setState({
        name: current.name,
        email: current.email
      })
    })
  }

  getUserInfo() {
    return axios.get('/user');
  }

  enableSubmission() {
    this.disabled = this.disabled.filter(field => field !== 'submit');
  }

  render() {
    return (
      <UserForm
        fields={this.fields}
        disabled={this.disabled}
        method="post"
        action="/edit"
        name={this.state.name}
        email={this.state.email}
        password={this.state.password}
        submit="Save"
        submitHandler={this.handleSubmit}
        changeHandler={this.handleChange}
      />
    );
  }
}

export default EditForm;